@keyframes fadeInDelay {
	0% {opacity: 0;}
	50% {opacity: 0;}
	100% {opacity: 1;}
}

.NavBar {
	width: 200px;
	position:fixed;
	right: 0;
	top: 160px;
	font-size: 2rem;
	animation: fadeInDelay 2s;
	z-index: 4;
}

nav a {
		text-decoration: none;
}

.nav-item-toggle-div {
	height: 60px;
	width: 100%;
	position: absolute;
	right: 0;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	z-index: 4;
}

.nav-toggle {
	height: 50px;
	width: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-image: url(/static/media/nav-back.336d3800.jpg);
	background-position: left;
	border: 1px solid ghostwhite;
	border-right: none;
	cursor: pointer;
	animation: moveInNav 1s;
	box-shadow: 2px 3px 13px 0px black;

}

.nav-toggle-icon {
	background-color: none;
	color: ghostwhite;
	transition: 500ms;
	transform: rotate(-90deg);
}


.nav-toggle-icon.rotate {
	transform: rotate(-180deg);
	transition: 500ms;
}

.nav-items {
	position: absolute;
	width: 160px;
	right: -100%;
	top: 70px;
	transition: 500ms;
	background-image: url(/static/media/nav-back.336d3800.jpg);
	background-size:cover;
	border: 1px solid rgb(255, 255, 255);
	border-right: none;
	z-index: 4;
	box-shadow: 2px 3px 13px 0px black;
}

.nav-items.show {
	right: 0;
	transition: 500ms;
}

.nav-item {
	background-color: transparent;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	height: 40px;
	border:none;
	color: ghostwhite;
	text-indent: 4rem;
	border-bottom: 2 solid blue;
	border-collapse: collapse;
}

.nav-item:hover {
	/* color: blue; */
	-webkit-text-decoration: underline ghostwhite;
	        text-decoration: underline ghostwhite;
}

@media screen and (max-width: 600px) {

	.nav-item-toggle-div {
		top: -50px;
	}

	.nav-items {
		top: 20px;
	}
}
*,
*::after,
*::before {
	margin:0;
	padding:0;
   box-sizing: border-box;
}

html {
	position: absolute;
	font-size: 10px;
	top: 0px;
	right: 0px;
	bottom: 0px;
	left: 0px;
	background-color: black;
	z-index: -2;
}

html:before {
	content: '';
	background-image: url(/static/media/stars.9f9fb4ad.jpg);
	position: absolute;
	margin: auto;
	top: 0px;
	right: 0px;
	bottom: 0px;
	left: 0px;
	opacity: .2;
	animation: fadeIn2 3s;
	pointer-events: none;
	z-index: -1;

}

